const express = require('express');
const app = express();
const PORT = 3000;

var auto = ['azul', 'negro', 'amarillo', 'rojo'];
// Route
app.get('/', (req, res) => {
    auto.map((d) => {
        console.log(d);
    });
    res.json({
        msg: 'Welcome to upload file'
    });
});


app.listen(PORT, () => {
    console.log(`App listening on port ${PORT}`);
});